import styled from 'styled-components';
import './translator.css';

import { useEffect, useState } from "react";

const Wrapper = styled.div`
  padding: 4em;
  background: papayawhip;
  margin-left: 300px;
  width: 770px;
  height: 366px;
  left: 65px;
  position: absolute;
  display: inline-block;
  border-radius: 26px 26px 0px 0px;
  align-items:center;
  z-index: 1;
`;

function Translator() {

    const [selectLang, setSelectLang] = useState("en")
    const [text, setText] = useState("")

    // 宣告一個物件
    // var original = { pageLanguage: selectLang};
    // function googleTranslateElementInit() {
    //     new window.google.translate.TranslateElement(original, 'google_translate_element');
    // }
    const googleTranslateElementInit = () => {
        new window.google.translate.TranslateElement({
            pageLanguage: selectLang,  layout: window.google.translate.TranslateElement.FloatPosition.TOP_LEFT,
            autoDisplay: true
        }, 'google_translate_element')
    }
    useEffect(() => {
        var addScript = document.createElement('script');
        addScript.setAttribute('src', '//translate.google.com/translate_a/element.js?cb=googleTranslateElementInit');
        document.body.appendChild(addScript);
        window.googleTranslateElementInit = googleTranslateElementInit;
    }, [])

    var array = ["en", "zh - TW", "ja", "ko", "ms"]

    return (
        <Wrapper>
            <body>
                <h1 translate="no">Translation</h1>

                <div id="selection" onChange={e => setSelectLang(array[e.target.options.selectedIndex - 1])}>
                    <select id="translating" translate="no">
                        <option value="en">English</option>
                        <option value="zh-TW">Chinese (Traditional)</option>
                        <option value="ja">Japanese</option>
                        <option value="ko">Korean</option>
                        <option value="ms">Malay</option>
                    </select>

                    <p translate="no">translate to</p>

                    <span id="google_translate_element" ></span>
                </div>

                <form action="">
                    <textarea onInput={e => setText(e.target.value)} id="input" placeholder="Hello..." translate="no" value={text} required></textarea>
                </form>

                <div id="translated">{text}</div>

            </body>
        </Wrapper>
    );
}

export default Translator;