import LoyaltyCard from "./Card/LoyaltyCard"

import { LineLogin } from 'reactjs-line-login';
import 'reactjs-line-login/dist/index.css';

function App() {
  const [open, setOpen] = useState("No");
  const [calculator, setCalculator] = useState(false);
  const [translator, setTranslator] = useState(false);
  const [exchanger, setExchanger] = useState(false);
  const [card, setCard] = useState(false);

  const [payload, setPayload] = useState(null);
  const [idToken, setIdToken] = useState(null);
  const [profile, setProfile] = useState(false);

  return (
    <div>
      {!payload && !idToken && (
        <Background >
          <LineLogin
            clientID='1656540054'
            clientSecret='ff748e610e6828ce50315b89e4efd160'
            state='b41c8fd15b895f0fc28bfwb9d7da89054d931e7s'
            nonce='d78a51235f6ee189e831q9c68523cfa336917ada'
            redirectURI='http://ChiHaoLu.github.io/Loyalty_Card_FinalBeta/'
            scope='profile openid email'
            setPayload={e => setPayload(e)}
            setIdToken={e => setIdToken(e)}
          />
        </Background>
      )}
      {payload && idToken && (
        <div>
          {console.log('payload', payload, 'idToken', idToken)}
          {open === "No" && (
            <Background >
              <Space><Title >Shida Night Market</Title></Space>
              <Wrapper>
                <Tec1 onClick={() => { setTranslator(true); setOpen("Yes"); }}>Translator</Tec1>
                <Tec2 onClick={() => { setExchanger(true); setOpen("Yes"); }}>Exchange Rate</Tec2>
                <Tec3 onClick={() => { setCalculator(true); setOpen("Yes"); }}>Calculator</Tec3>
                <Tec4 onClick={() => { setCard(true); setOpen("Yes"); }}>Loyalty Card</Tec4>
                <Tec5 >OpenChat<br />(Not for<br />Beta Ver.)</Tec5>
                <Tec6 onClick={() => { setProfile(true); setOpen("Yes"); }}>Hi, {idToken.name}</Tec6>
              </Wrapper>
              <Card></Card>
            </Background>
          )}
          {
            open === "Yes" && calculator && (
              <Background>
                <Close onClick={() => { setOpen("No"); setCalculator(false); }} />
                <Calculator />
              </Background>
            )}
          {
            open === "Yes" && translator && (
              <Background>
                <Close onClick={() => { setOpen("No"); setTranslator(false); }} />
                <Translator />
              </Background>
            )}
          {
            open === "Yes" && exchanger && (
              <Background>
                <Close onClick={() => { setOpen("No"); setExchanger(false); }} />
                <Exchanger_New />
              </Background>
            )}
          {
            open === "Yes" && card && (
              <Background>
                <Close onClick={() => { setOpen("No"); setCard(false); }} />
                <Space1><Title1>Shida Night Market<br />Loyalty Card</Title1></Space1>
                <LoyaltyCard />
              </Background>
            )}
          {
            open === "Yes" && profile && (
              <Background>
                <Close onClick={() => { setOpen("No"); setProfile(false); }} />
                <Space1><Title2>{idToken.name}<br />{idToken.email}</Title2></Space1>
                <Wrapper style={{ backgroundColor: "#2f4f4f", backgroundImage: `url(${idToken.picture})` }} />
              </Background>
            )}
        </div>
      )}
    </div>
  );
}

export default App;

