const Prize_in = styled.button`
  background: #FFFFFF;
  width: 145px;
  height: 43px;
  top: 16px;
  left: 26px;
  position: absolute;
  display: inline-block;
  align-items:center;
  font-weight: bold;
  color: #1A638C;

  animation: rotate 1.5s linear infinite;
  @keyframes rotate{
      0%{
        filter: hue-rotate(0deg);
      }
      100%{
        filter: hue-rotate(360deg);
      }
    }
`;

