import styled from 'styled-components';
import './exchanger.css';
import $ from 'jquery';
// import axios from 'axios';

import { useEffect, useState } from "react";

const Wrapper = styled.div`
  padding: 4em;
  background: papayawhip;
  margin-left: 300px;
  width: 370px;
  height: 316px;
  left: 65px;
  position: absolute;
  display: inline-block;
  border-radius: 26px 26px 0px 0px;
  align-items:center;
  z-index: 1;
`;

function Exchanger() {

    const [value, setValue] = useState(0)
    const [ans, setAns] = useState(0)
    const [select, setSelect] = useState("TWD")
    const [final, setFinal] = useState("TWD")

    var crrncy = {
        'TWD': { 'USD': 0.036, 'EUR': 0.031, 'KRW': 42.6, 'JPY': 3.99, 'TWD': 1 },
        'USD': { 'TWD': 28.05, 'EUR': 0.86, 'KRW': 1195.47, 'JPY': 111.88, 'USD': 1 },
        'EUR': { 'TWD': 32.45, 'USD': 1.16, 'KRW': 1382.89, 'JPY': 129.41, 'EUR': 1 },
        'KRW': { 'TWD': 0.023, 'USD': 0.00084, 'EUR': 0.00072, 'JPY': 0.094, 'KRW': 1 },
        'JPY': { 'TWD': 0.25, 'USD': 0.0089, 'EUR': 0.0077, 'KRW': 10.69, 'JPY': 1 },
    }

    var array = ["TWD", "USD", "EUR", "KRW", "JPY"]

    function finc(a, b) {
        console.log("from", a, "to", b)
        setAns(Math.round(value * crrncy[a][b] * 100) / 2)
    }

    return (
        <Wrapper>
            <body>
                <header>
                    <h3>Transfer</h3>
                </header>

                <main>
                    <input name="money" class="top" placeholder="Please fill in the number..." 
                            onInput={(e) => {
                                setValue(e.target.value);
                                finc(select, final);
                                }} />
                    <br />
                    <div id="exchange">Exchange from ... to ...</div>
                    <div id="money_type">
                        <select name="exchange" id="exchange_from" onChange={(e) => {
                            var x = e.target.options.selectedIndex;
                            setSelect(array[x]);
                            finc(array[x], final);
                        }}>
                            <option>TWD</option>
                            <option>USD</option>
                            <option>EUR</option>
                            <option>KRW</option>
                            <option>JPY</option>
                        </select>
                        <select name="exchange" id="exchange_to" onChange={(e) => {
                            var x = e.target.options.selectedIndex;
                            setFinal(array[x]);
                            finc(select, array[x]);
                        }}>
                            <option>TWD</option>
                            <option>USD</option>
                            <option>EUR</option>
                            <option>KRW</option>
                            <option>JPY</option>
                        </select>
                    </div>
                    <div id="outcome" class="bottom" placeholder="Outcome..." >{ans}</div>
                </main>
            </body>
        </Wrapper>
    );
}

