import styled from 'styled-components';
import './calculator.css';
import { useEffect, useState } from "react";
import { BigEval } from "bigeval";

const Wrapper = styled.div`
  padding: 4em;
  background: papayawhip;
  margin-left: 300px;
  width: 370px;
  height: 316px;
  left: 65px;
  position: absolute;
  display: inline-block;
  border-radius: 26px 26px 0px 0px;
  align-items:center;
  z-index: 1;
`;

function Calculator() {

    const [res, setRes] = useState("")

    function dis(val) {
        // res += val;
        setRes(res + val)
        console.log(res)
    }


    var BigEval = require('bigeval')
    var Obj = new BigEval();
    //function that evaluates the digit and return result
    function solve() {
        if (res === undefined || res === null || Obj.err ){
            // res = ""
            setRes("")
        }
        if (typeof (res) === "string" && (res.includes("Nan") || res.includes("ERROR") || res.includes("undefined"))){
            var str = res
            setRes(str.replace('ERROR', ''))
            str = res
            setRes(str.replace('undefined', ''))
        }
        setRes(Obj.exec(res))
        if (res === undefined || res === null || Obj.err ) {
            setRes("")
        }
        console.log(typeof(res))
        if (typeof (res) === "string" && (res.includes("Nan") || res.includes("ERROR") || res.includes("undefined"))) {
            var str = res
            setRes(str.replace('ERROR', ''))
            str = res
            setRes(str.replace('undefined', ''))
        }
        console.log(res)
    }

    //function that clear the display
    function clr() {
        setRes("0")
        console.log(res)
    }

    useEffect(() => {
        if (res === undefined || res === null || Obj.err ) {
            setRes("")
        }
        if (typeof (res) === "string" && (res.includes("Nan") || res.includes("ERROR") || res.includes("undefined"))) {
            var str = res
            setRes(str.replace('ERROR', ''))
            str = res
            setRes(str.replace('undefined', ''))
        }
    }, [])

    return (
        <Wrapper>
            <table>
                <tr>
                    <td align="center" colspan="6">
                        <div class="output" align="right" >{res}</div>
                    </td>
                </tr>
                <tr align="center">
                    <td >    <input class="button" type="button" value="7" onClick={() => { dis('7')}} />    </td>
                    <td >    <input class="button" type="button" value="8" onClick={() => dis('8')} />   </td>
                    <td >    <input class="button" type="button" value="9" onClick={() => dis('9')} />       </td>
                    <td >    <input class="button calculate" type="button" value="+" onClick={() => dis('+')} />       </td>
                    <td >    <input class="button calculate" id="mC" type="button" value="C" onClick={() => clr()} />       </td>
                </tr>
                <tr align="center">
                    <td>    <input class="button" type="button" value="4" onClick={() => dis('4')} />  </td>
                    <td>    <input class="button" type="button" value="5" onClick={() => dis('5')} />  </td>
                    <td>    <input class="button" type="button" value="6" onClick={() => dis('6')} />  </td>
                    <td>    <input class="button calculate" type="button" value="-" onClick={() => dis('-')} /> </td>
                    <td>    <input class="button calculate" type="button" value="%" onClick={() => { dis('*0.01'); solve()}} /> </td>
                </tr>
                <tr align="center">
                    <td>    <input class="button" type="button" value="1" onClick={() => dis('1')} />  </td>
                    <td>    <input class="button" type="button" value="2" onClick={() => dis('2')} />  </td>
                    <td>    <input class="button" type="button" value="3" onClick={() => dis('3')} />  </td>
                    <td>    <input class="button calculate" type="button" value="*" onClick={() => dis('*')} /> </td>
                </tr>
                <tr align="center">
                    <td>    <input class="button" type="button" value="0" onClick={() => dis('0')} />  </td>
                    <td>    <input class="button" type="button" value="." onClick={() => dis('.')} />  </td>
                    <td>    <input class="button equal" type="button" value="=" onClick={() => solve()} />  </td>
                    <td>    <input class="button calculate" type="button" value="/" onClick={() => dis('/')} /> </td>
                </tr>

            </table>
        </Wrapper>
    );
}

export default Calculator;